body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  min-height: 100vh;
  background-color: #1a1f29;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

/* App header / title. */
.App-header {
  -webkit-flex-grow: 0;
          flex-grow: 0;
  padding: 1rem;
  font-size: 3rem;
  color: whitesmoke;
  border-bottom: 1px solid whitesmoke;
  border-left-width: 10;
  text-align: center;
}

/* Displays API Source */
.App-api {
  color: darkgrey;
  padding: 1rem;
  text-align: right;
  -webkit-align-self: flex-end;
          align-self: flex-end;
}

/* Api link. */
a {
  color:darkgrey;
  text-decoration:none;
}


/* Container that holds the quote */
.quoteContainer {
    width: 75%;
    -webkit-flex-grow: 4;
            flex-grow: 4;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

/* Line after the container at the bottom */
.quoteContainer::after {
    background-color: darkgrey; 
    content: '';
    height: 1px;
    width: 50px;
}

/* Quote text. */
.quoteText {
    margin-top: 15vh;
    color: whitesmoke;
    font-size: 1.5rem;
    text-indent: 3rem;
    white-space: pre-wrap;
    text-align: left;
    padding-right: 5px;
}

/* Quote author name */
.quoteAuthor {
    margin: 1rem;
    text-align: right;
    color: lightgray;
}


